.main-page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-image: url("../img/main-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center center;
}

.main-page__header {
  margin-bottom: 112px;

  @include media-mobile {
    margin-bottom: 362px;
  }
}
